@import url('https://fonts.googleapis.com/css2?family=Acme&family=Indie+Flower&family=Lato&family=Open+Sans&display=swap');
.main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.question {
    font-family: 'Acme', sans-serif;
    font-size: 22px;
}

.img {
    width: 100px;
}

.logo {
    width: 300px;
    margin-top: 90px;
    margin-bottom: 10px;
}

body {
    background-image: url("https://img3.goodfon.com/wallpaper/nbig/1/85/priroda-peyzazh-derevo-derevya-845.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

.start {
    padding: 20px 30px;
    opacity: 0;
    border: none;
    box-shadow: inset 0 0 15px #7dfc6d, 0 0 30px #021101;
    border-radius: 10px;
    font-family: 'Acme', sans-serif;
    font-size: 16px;
    width: 15%;
    cursor: pointer;
    outline: none;
}

.questionsContainer {
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.questionButton {
    margin: 10px;
    padding: 12px;
    box-shadow: inset 0 0 15px #caf13b, 0 0 7px #021101;
    font-family: 'Rubik', sans-serif;
    font-size: 14px;
    border: none;
    cursor: pointer;
    outline: none;
    border-radius: 4px;
}

.card {
    width: 50%;
}

@media(max-width: 1000px) {
    .card {
        width: 60%;
    }
    .start {
        width: 20%;
    }
    body {
        background-color: rgb(227, 253, 182);
        background-image: none;
    }
    .logo {
        width: 200px;
        margin-top: 100px;
        margin-bottom: 15px;
    }
}

@media(max-width: 700px) {
    .card {
        width: 70%;
    }
    .start {
        width: 27%;
    }
    body {
        background-color: rgb(227, 253, 182);
        background-image: none;
    }
    .logo {
        width: 150px;
        margin-top: 100px;
        margin-bottom: 5px;
    }
}

@media(max-width: 500px) {
    .card {
        width: 80%;
    }
    .start {
        width: 50%;
    }
}

.next {
    width: 200px;
    margin: 5px;
    padding: 10px;
    -webkit-box-shadow: inset 0 0 5px #5df73e;
    box-shadow: inset 0 0 5px #5df73e, 0 0 15px #021101;
    font-size: 14px;
    border: none;
    cursor: pointer;
    outline: none;
    font-family: 'Acme', sans-serif;
    border-radius: 4px;
}

.correct {
    -webkit-box-shadow: inset 0 0 15px #03f610;
    box-shadow: inset 0 0 15px#03f610;
    background-color: #a0f8a4;
}

.incorrect {
    -webkit-box-shadow: inset 0 0 15px #f82c08;
    box-shadow: inset 0 0 15px #f82c08;
    background-color: #fcb2a5;
}

.score {
    font-family: 'Acme', sans-serif;
    font-size: 22px;
}